/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n'
import { applyFilters, addFilter } from '@wordpress/hooks'
import { compose } from '@wordpress/compose'
import { Fragment } from '@wordpress/element'
import { i18n } from 'cartzillagb'
import {
	PanelBody, ToggleControl, TextControl 
} from '@wordpress/components'
import {
	RichText
} from '@wordpress/block-editor'

/**
 * Internal dependencies
 */
import { name } from './index'

/**
 * External dependencies
 */
import {
	BackgroundSettings, ButtonControlsHelper, ButtonEditHelper, ImageUploadPlaceholder
} from '~cartzillagb/components'
import { withTabbedInspector, withClickOpenInspector } from '~cartzillagb/higher-order'
import classnames from 'classnames'

addFilter( 'cartzillagb.banner.edit.inspector.style.before', 'czgb/banner', ( output, props ) => {
	const { 
		setAttributes,  
	} = props

	const {
		hideLeftbanner,
		hideRightbanner,
		backgroundType,
		backgroundColor,
		backgroundImageURL,
		background,
		enableButton,
		enableImage,
		bgcolor,
		custombgcolor,
		enableButton1,
		enableButton2,
		containerClass,
		enableContainer

	} = props.attributes

	return (
		<Fragment>
			<PanelBody title={ __( 'Banner Display Settings', i18n ) }>
				<ToggleControl
					label={ __( 'Hide Left Banner', i18n ) }
					checked={ hideLeftbanner }
					onChange={ hideLeftbanner => setAttributes( { hideLeftbanner } ) }
				/>
				<ToggleControl
					label={ __( 'Hide Right Banner', i18n ) }
					checked={ hideRightbanner }
					onChange={ hideRightbanner => setAttributes( { hideRightbanner } ) }
				/>
				<ToggleControl
					label={ __( 'Display Button #1', i18n ) }
					checked={ enableButton1 }
					onChange={ enableButton1 => setAttributes( { enableButton1 } ) }
				/>

				<ToggleControl
					label={ __( 'Display Button #2', i18n ) }
					checked={ enableButton2 }
					onChange={ enableButton2 => setAttributes( { enableButton2 } ) }
				/>

				<ToggleControl
					label={ __( 'Enable Container', i18n ) }
					checked={ enableContainer }
					onChange={ enableContainer => setAttributes( { enableContainer } ) }
				/>
				<TextControl
	                label={ __( 'Enter Class' ) }
	                value={ containerClass }
	                onChange={ ( containerClass ) => setAttributes( { containerClass } ) }
	            />
			</PanelBody>
			<PanelBody title={ __( 'Banner #1 Background Settings', i18n ) } initialOpen={ false }>
				<BackgroundSettings
					background={ bgcolor }
					backgroundColor={ custombgcolor }
					onChangeBackground={ bgcolor => setAttributes( { bgcolor } ) }
					onChangeBackgroundColor={ custombgcolor => setAttributes( { custombgcolor: custombgcolor.hex } ) }
					onRemoveBackground={ () => { setAttributes( { bgcolor: '', custombgcolor: '' } ) } }
				/>
				<ToggleControl
					label={ __( 'Display Banner Image', i18n ) }
					checked={ enableImage }
					onChange={ enableImage => setAttributes( { enableImage } ) }
				/>
			</PanelBody>
			<PanelBody title={ __( 'Banner #2 Background Settings', i18n ) } initialOpen={ false }>
				<BackgroundSettings
					backgroundLabel={ __("Background Image", i18n ) }
					backgroundType={ backgroundType !== undefined ? backgroundType : '' }
					background={ background }
					backgroundColor={ backgroundColor }
					backgroundImageURL={ backgroundImageURL }
					onChangeBackgroundType={ backgroundType => setAttributes( { backgroundType } ) }
					onChangeBackground={ background => setAttributes( { background: ` ${ background }`, backgroundColor: '' } ) }
					onChangeBackgroundColor={ backgroundColor => setAttributes( { backgroundColor: backgroundColor.hex, background: '' } ) }
					onChangeBackgroundImage={ ( { url } ) => setAttributes( { backgroundImageURL: url } ) }
					onRemoveBackgroundImage={ () => {
						setAttributes( { backgroundImageURL: '' } )
					} }
					onRemoveBackground={ () => { setAttributes( { background: '', backgroundColor: '' } ) } }

				/>
			</PanelBody>
			<PanelBody title={ __( 'Button Settings #1', i18n ) }initialOpen={ false } >
				<ButtonControlsHelper
					attrNameTemplate="button1%s"
					setAttributes={ setAttributes }
					blockAttributes={ props.attributes }
				/>
			</PanelBody>

			<PanelBody title={ __( 'Button Settings #2', i18n ) }initialOpen={ false } >
				<ButtonControlsHelper
					attrNameTemplate="button2%s"
					setAttributes={ setAttributes }
					blockAttributes={ props.attributes }
				/>
			</PanelBody>
		</Fragment>
	)
} )

const edit = props => {
	const { 
		attributes, 
		setAttributes, 
		className
	} = props 

	const {
		banner1Pretitle,
		banner1Title,
		banner2Title,
		banner2Desc,
		hideLeftbanner,
		hideRightbanner,
		mediaURL,
		backgroundColor,
		backgroundImageURL,
		background,
		enableImage,
		enableButton1,
		enableButton2,
		enableContainer,
		bgcolor,
		custombgcolor,
		containerClass
	} = attributes

	const mainClasses = classnames( [
		className,
	], applyFilters( `cartzillagb.${ name.replace( 'czgb/', '' ) }.main-block.classes`, {}, props ) )

	const styleAttr = {}

	if ( backgroundImageURL || backgroundColor ) {
		styleAttr.style = {
			backgroundImage: `url( ${ backgroundImageURL } )`,
			//backgroundColor: backgroundColor !== undefined ? backgroundColor : ''

			backgroundColor: backgroundColor && backgroundColor, 
		}
	}

	const bgstyle = { backgroundColor: custombgcolor && custombgcolor }

	return (
		<div className={ mainClasses }>
			<section className={ `banner ${ enableContainer === true ? 'container ': '' }${ containerClass }` }>
				<div className="row">
					{ hideLeftbanner &&
					<div className={ `col-md-${ hideRightbanner === false ? '12' : '8' } mb-4` } >
						<div className={ `d-sm-flex justify-content-between align-items-center overflow-hidden rounded-lg ${ bgcolor ? bgcolor : custombgcolor ? '' : 'bg-secondary' }` } style={ bgstyle } >
							<div className="py-4 my-2 my-md-0 py-md-5 px-4 ml-md-3 text-center text-sm-left">
								<RichText
									tagName="h4"
									className="font-size-lg font-weight-light mb-2" 
									value={ banner1Pretitle || 'Hurry up! Limited time offer' }
									onChange={ banner1Pretitle => setAttributes( { banner1Pretitle : banner1Pretitle } ) }
									placeholder={ __( 'banner1Pretitle', i18n ) }
									keepPlaceholderOnFocus
								/>
								<RichText
									tagName="h3"
									className="mb-4" 
									value={ banner1Title || 'Converse All Star on Sale' }
									onChange={ banner1Title => setAttributes( { banner1Title : banner1Title } ) }
									placeholder={ __( 'banner1Title', i18n ) }
									keepPlaceholderOnFocus
								/>
								{ enableButton1 &&
									<ButtonEditHelper
										attrNameTemplate={ `button1%s` }
										setAttributes={ setAttributes }
										blockAttributes={ props.attributes }
									/>
								}
							</div>
							{ enableImage &&
								<ImageUploadPlaceholder
									className={ `d-block ml-auto` }
									imageURL={ mediaURL }
									onRemove={ () => {
										setAttributes( {
											mediaURL: '',
										} )
									} }
									onChange={ ( { url } ) => {
										setAttributes( {
											mediaURL: url,
										} )
									} }
									hasRemove={ false }
									render={ <img src={ mediaURL } alt={ __( 'Shop Converse' ) } /> }
								/>
							}
						</div>
					</div>
					}

					{ hideRightbanner &&
						<div className={ `col-md-${ hideLeftbanner === false ? '12' : '4' } mb-4` }>
							<div className={ `d-flex flex-column h-100 justify-content-center bg-size-cover bg-position-center rounded-lg ${ background }` } { ...styleAttr }>
								<div className="py-4 my-2 px-4 text-center">
									<div className="py-1">
										<RichText
											tagName="h5"
											className="mb-2" 
											value={ banner2Title || 'Last Gen iPad Pro' }
											onChange={ banner2Title => setAttributes( { banner2Title : banner2Title } ) }
											placeholder={ __( 'Banner 2 title', i18n ) }
											keepPlaceholderOnFocus
										/>
										<RichText
											tagName="p"
											className="font-size-sm text-muted" 
											value={ banner2Desc || 'Last Gen iPad Pro' }
											onChange={ banner2Desc => setAttributes( { banner2Desc : banner2Desc } ) }
											placeholder={ __( 'Banner 2 description', i18n ) }
											keepPlaceholderOnFocus
										/>
										{ enableButton2 &&
											<ButtonEditHelper
												attrNameTemplate={ `button2%s` }
												setAttributes={ setAttributes }
												blockAttributes={ props.attributes }
											/>
										}
									</div>
								</div>
							</div>
						</div>
					}
				</div>
			</section>
		</div>
	)
}



export default compose(
	withTabbedInspector( [ 'style', 'advanced' ] ),
	withClickOpenInspector( [
		[ '.czgb-button', 'button1' ],
		[ '.czgb-button', 'button2' ],
	] )
)( edit )