import $ from 'jquery'

$( document ).on( 'cartzillagb_products_block_output_success', function( e, el ) {
	$( el ).each( function() {
		if( $( this ).closest( '.wp-block-czgb-products-carousel' ).length && typeof $( this ).closest( '.wp-block-czgb-products-carousel' ).data( 'slick-attributes' ) !== undefined && $( this ).find( '.products' ).length ) {
			if( $( this ).find( '.products' ).attr( 'data-slick' ) === undefined ) {
				let slickAttributes = JSON.parse( $( this ).closest( '.wp-block-czgb-products-carousel' ).attr( 'data-slick-attributes' ) )
				const slickClasses = $( this ).closest( '.wp-block-czgb-products-carousel' ).data( 'slick-classes' )
				const slickId = $( this ).closest( '.wp-block-czgb-products-carousel' ).attr( 'id' )

				if( slickAttributes && slickAttributes.customArrows ) {
					if( $( this ).closest( '.wp-block-czgb-products-carousel' ).find( '.slick-arrow' ) && $( this ).closest( '.wp-block-czgb-products-carousel' ).find( '.slick-arrow' ).length > 0 ) {
						$( this ).closest( '.wp-block-czgb-products-carousel' ).find( '.slick-arrow' ).remove()
					}
					slickAttributes = { ...slickAttributes, appendArrows: `#${slickId} ${slickAttributes.customArrows}` }
				}

				if( slickAttributes && $('body,html').hasClass('rtl') ) {
					slickAttributes = { ...slickAttributes, rtl: true }
				}

				$( this ).find( '.products' ).attr( 'data-slick', JSON.stringify( {...slickAttributes} ) )
				$( this ).find( '.products' ).addClass( slickClasses )
				$( this ).find( '.products' ).slick()
				
			}
		}
	})
})