/**
 * External dependencies
 */
import { HeaderIcon } from '~cartzillagb/icons'
import { range, createButtonAttributes } from '~cartzillagb/util'
import {
    disabledBlocks, i18n 
} from 'cartzillagb'

/**
 * Internal dependencies
 */
import save from './save'
import edit from './edit'


/**
 * WordPress dependencies
 */
import { applyFilters } from '@wordpress/hooks'
import { __ } from '@wordpress/i18n'

export const schema = {
    blockId: {
        type: 'string',
        default: '',
    },
    limit: {
        type: 'number',
        default: 1,
    },
    bannerlimit: {
        type: 'number',
        default: 3,
    },
    bgcolor: {
        type: 'string',
    },
    custombgcolor: {
        type: 'string',
    },
    enableButton: {
        type: 'boolean',
        default: true
    },

    ...createButtonAttributes( 'button1%s', { 
        defaultText: 'Shop Now',
        iconAfterText: true,
        isShadow: true,
        defaultIcon: 'czi-arrow-right',
        defaultAnimation: 'scale-up',
        defaultDelay: 'delay-4' 
    } ),
    ...createButtonAttributes( 'button2%s', { 
        defaultText: 'Shop Now',
        iconAfterText: true,
        isShadow: true,
        defaultIcon: 'czi-arrow-right',
        defaultAnimation: 'scale-up',
        defaultDelay: 'delay-4' 
    } ),
    ...createButtonAttributes( 'button3%s', { 
        defaultText: 'Shop Now',
        iconAfterText: true,
        isShadow: true,
        defaultIcon: 'czi-arrow-right',
        defaultAnimation: 'scale-up',
        defaultDelay: 'delay-4' 
    } ),
    ...createButtonAttributes( 'button4%s', { 
        defaultText: 'Shop Now',
        iconAfterText: true,
        isShadow: true,
        defaultIcon: 'czi-arrow-right',
        defaultAnimation: 'scale-up',
        defaultDelay: 'delay-4' 
    } ),
    ...createButtonAttributes( 'button5%s', { 
        defaultText: 'Shop Now',
        iconAfterText: true,
        isShadow: true,
        defaultIcon: 'czi-arrow-right',
        defaultAnimation: 'scale-up',
        defaultDelay: 'delay-4' 
    } ),
}

const Maxlimit = applyFilters( 'cartzillagb.hero-carousel-2.icon.edit.inspector.max.limit', 5 )

{ range( 1, Maxlimit + 1 ).map( i => {
    schema[ `pretitle${ i }` ] = {
        type: 'string',
    }
    schema[ `title${ i }` ] = {
        type: 'string',
    }
    schema[ `subtitle${ i }` ] = {
        type: 'string',
    }
    schema[ `bgcolor${ i }` ] = {
        type: 'string',
    }
    schema[ `bgcustomcolor${ i }` ] = {
        type: 'string',
    }
    schema[ `mediaURL1${ i }` ] = {
        type: 'string',
    }
    schema[ `enablepreTitle${ i }` ] = {
        type: 'boolean',
        default: true,
    }
    schema[ `enableTitle${ i }` ] = {
        type: 'boolean',
        default: true,
    }
    schema[ `enablesubTitle${ i }` ] = {
        type: 'boolean',
        default: true,
    }
    return ""
} ) }

const BannerMaxlimit = applyFilters( 'cartzillagb.hero-carousel-2.icon.edit.inspector.max.bannerlimit', 3 ) 

{ range( 1, BannerMaxlimit + 1 ).map( j => {
    schema[ `bannertitle${ j }` ] = {
        type: 'string',
    }
    schema[ `shoplink${ j }` ] = {
        type: 'string',
    }
    schema[ `link${ j }` ] = {
        type: 'string',
    }
    schema[ `mediaURL2${ j }` ] = {
        type: 'string',
    }
    schema[ `bannerbgcolor${ j }` ] = {
        type: 'string',
    }
    schema[ `bannercustombgcolor${ j }` ] = {
        type: 'string',
    }
    schema[ `bannertextcolor${ j }` ] = {
        type: 'string',
    }
    schema[ `custombannertextcolor${ j }` ] = {
        type: 'string',
    }
        return ""
} ) }

export const name = 'czgb/hero-carousel-2'

export const settings = {
    title: __( 'Hero Carousel #2', i18n ),
    description: __( 'Start building your website with Hero carousel #2.', i18n ),
    icon: HeaderIcon,
    category: 'cartzillagb',
    example: {},
    keywords: [
        __( 'Hero Carousel 2', i18n ),
        __( 'CartzillaGb', i18n ),
    ],
    attributes: schema,
    supports: {
        inserter: ! disabledBlocks.includes( name ), // Hide if disabled.
    },

    edit,
    save,

    // CartzillaGB modules.
    modules: {
        'advanced-responsive': true,
        'custom-css': {
            default: applyFilters( 'cartzillagb.hero-carousel-2.custom-css.default', '' ),
        },
    },
}