/**
 * External dependencies
 */
import { BlogPostsIcon } from '~cartzillagb/icons'
import { range } from '~cartzillagb/util'
import {
	disabledBlocks, i18n, srcUrl
} from 'cartzillagb'

/**
 * Internal dependencies
 */
import save from './save'
import edit from './edit'
import DefaultImage  from  './img/img1.png'


/**
 * WordPress dependencies
 */
import { applyFilters } from '@wordpress/hooks'
import { __ } from '@wordpress/i18n'


export const schema = {
	className : {
		type: 'string',
	},
	enableContainer: {
		type: 'boolean',
		default: true,
	},
	limit: {
		type: 'number',
		default: 6,
	},
	smColumns : {
		type: 'number',
		default: 2,
	},
	title : {
		type: 'string',
	},
	subTitle : {
		type: 'string',
	},
	displaySectionHeader : {
		type: 'boolean',
		default: true,
	},
	displaySectionTitle : {
		type: 'boolean',
		default: true,
	},
	displaySectionDesc : {
		type: 'boolean',
		default: true,
	},
	enableIcon : {
		type: 'boolean',
		default: true,
	},
	
}

const maxLimit = applyFilters( 'cartzillagb.cards.edit.inspector.max.limit', 6 )

{ range( 1, maxLimit + 1 ).forEach( i => {

	schema[ `mediaURL${ i }` ] = {
		type: 'string',
		default: `${ srcUrl }/${ DefaultImage }`
	}
	schema[ `name${ i }` ] = {
		type: 'string',
		
	}
	schema[ `link${ i }` ] = {
        type: 'string',
    }
} ) }



export const name = 'czgb/cards'

export const settings = {
	title: __( 'Cards Block', i18n ),
	description: __( 'Describe a single subject in a small card. You can use this to describe your product, service or a person.', i18n ),
	icon: BlogPostsIcon,
	category: 'cartzillagb',
	example: {},
	keywords: [
		__( 'cards', i18n ),
		__( 'CartzillaGb', i18n ),
	],
	attributes: schema,
	supports: {
		inserter: ! disabledBlocks.includes( name ), // Hide if disabled.
	},

	edit,
	save,

	// CartzillaGB modules.
	modules: {
		'advanced-responsive': true,
	},
}