/**
 * Internal dependencies
 */
import './auto-block-recovery'
import { default as DebugSidebar } from './debug-sidebar'

/**
 * WordPress dependencies
 */
import { registerPlugin } from '@wordpress/plugins'

/**
 * External dependencies
 */
import { ConvertToContainerButton, GetBlockAttributesButton } from '~cartzillagb/components'
import { devMode } from 'cartzillagb'

registerPlugin( 'cartzillagb-convert-to-container-button', { render: ConvertToContainerButton } )

if ( devMode ) {
	registerPlugin( 'cartzillagb-block-tester', {
		icon: 'clipboard',
		render: DebugSidebar,
	} )
	registerPlugin( 'cartzillagb-block-attributes-get-button', { render: GetBlockAttributesButton } )
}
