/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n'
import { disabledBlocks, i18n } from 'cartzillagb'
import { applyFilters } from '@wordpress/hooks'

/**
 * Internal dependencies
 */
import edit from './edit'
import save from './save'
import { InfoIcon } from '~cartzillagb/icons'
import { range } from '~cartzillagb/util'

export const schema = {
    enableTitle: {
        type: 'boolean',
        default: true,
    },
    enableSubtitle: {
        type: 'boolean',
        default: true,
    },
    enableIcon: {
        type: 'boolean',
        default: true,
    },
    limit: {
        type: 'number',
        default: 2,
    },
    'lgColumns': {
        type: 'number',
        default: 2,
    },
    'mdColumns': {
        type: 'number',
        default: 2,
    },
}

const MaxListlimit = applyFilters( 'cartzillagb.info-card.limit.edit.inspector.max.limit', 12 )

{ range( 1, MaxListlimit + 1 ).map( i => {
    schema[ `title${ i }` ] = {
        type: 'string',
    }
    schema[ `titlecolor${ i }` ] = {
        type: 'string',
    }
    schema[ `customtitlecolor${ i }` ] = {
        type: 'string',
    }
    schema[ `subtitle${ i }` ] = {
        type: 'string',
    }
    schema[ `subtitlecolor${ i }` ] = {
        type: 'string',
    }
    schema[ `customsubtitlecolor${ i }` ] = {
        type: 'string',
    }
    schema[ `bgcolor${ i }` ] = {
        type: 'string',
    }
    schema[ `bgcustomcolor${ i }` ] = {
        type: 'string',
    }
    schema[ `icon${ i }` ] = {
        type: 'string',
    }
    schema[ `link${ i }` ] = {
        type: 'string',
    }
    return ""
} ) }

export const name = 'czgb/info-card'

export const settings = {
    title: __( 'Info Card', i18n ),
    description: __( 'Start building your website with info card.', i18n ),
    icon: InfoIcon,
    category: 'cartzillagb',
    example: {},
    keywords: [
        __( 'Info Card', i18n ),
        __( 'CartzillaGB', i18n ),
    ],
    attributes: schema,
    supports: {
        inserter: ! disabledBlocks.includes( name ), // Hide if disabled.
    },

    edit,
    save,

    // CartzillaGB modules.
    modules: {
        'advanced-responsive': true,
    },
}