/**
 * External dependencies
 */
import { createAllCombinationAttributes, __getValue } from '~cartzillagb/util'

/**
 * WordPress dependencies
 */
import { addFilter, doAction } from '@wordpress/hooks'
import { AlignmentToolbar, BlockControls } from '@wordpress/block-editor'
import deepmerge from 'deepmerge'
import { Fragment } from '@wordpress/element'

// Add contentAlign attributes
const addAttributes = attributes => {
	return {
		...attributes,
		...createAllCombinationAttributes(
			'%sContentAlign', {
				type: 'string',
				default: '',
			},
			[ '', 'Tablet', 'Mobile' ]
		),
	}
}

// Align all the block contents, including block titles.
const addStyles = ( styleObject, props ) => {
	const getValue = __getValue( props.attributes )

	const styles = [ styleObject ]
	styles.push( {
		'.czgb-inner-block': {
			textAlign: getValue( 'contentAlign' ),
		},
		tablet: {
			'.czgb-inner-block': {
				textAlign: getValue( 'tabletContentAlign' ),
			},
		},
		mobile: {
			'.czgb-inner-block': {
				textAlign: getValue( 'mobileContentAlign' ),
			},
		},
	} )

	return deepmerge.all( styles )
}

const addAlignToolbar = ( output, props ) => {
	const { setAttributes } = props
	const {
		contentAlign = '',
	} = props.attributes
	return (
		<Fragment>
			{ output }
			<BlockControls>
				<AlignmentToolbar
					value={ contentAlign }
					onChange={ contentAlign => setAttributes( { contentAlign } ) }
				/>
			</BlockControls>
		</Fragment>
	)
}

const contentAlign = blockName => {
	addFilter( `cartzillagb.${ blockName }.edit.inspector.before`, `cartzillagb/${ blockName }/content-align`, addAlignToolbar, 11 )
	addFilter( `cartzillagb.${ blockName }.attributes`, `cartzillagb/${ blockName }/content-align`, addAttributes )
	addFilter( `cartzillagb.${ blockName }.styles`, `cartzillagb/${ blockName }/content-align`, addStyles, 9 )
	doAction( `cartzillagb.module.content-align`, blockName )
}

export default contentAlign
