import { __, sprintf } from '@wordpress/i18n'
import { Component, Fragment } from '@wordpress/element'
import {
	UP,
	DOWN,
	ENTER
} from '@wordpress/keycodes'
import {
	Spinner,
	Popover,
	IconButton
} from '@wordpress/components'
import { withInstanceId } from '@wordpress/compose'
import { withSelect } from '@wordpress/data'
import apiFetch from '@wordpress/api-fetch'
import { addQueryArgs } from '@wordpress/url'
import { i18n } from 'cartzillagb'

const stopEventPropagation = event => event.stopPropagation()

// const subtypeStyle = {
// 	border: '3px solid lightgrey',
// 	padding: '5px',
// 	borderRadius: '7px',
// 	marginRight: '10px',
// 	fontSize: '80%'
// }

function debounce(func, wait = 100) {
	let timeout
	return function(...args) {
		clearTimeout(timeout)
		timeout = setTimeout(() => {
			func.apply(this, args)
		}, wait)
	}
}

class UserSelector extends Component {

	/**
	 * ===== Available Props =======
	 *
	 * users <Array> of Post Objects, must include ID and name.
	 * data <Array> array of user properties to return (top level only right now)
	 * userType = <String> singular name of user type to restrict results to.
	 * onUserSelect <Function> callback for when a new user is selected.
	 * onChange <Function> callback for when users are deleted or rearranged.
	 * limit <Number> limit selection to users to X number of users.
	 *
	 */
	constructor() {
		super(...arguments)
		this.onChange = this.onChange.bind(this)
		this.onKeyDown = this.onKeyDown.bind(this)
		this.bindListNode = this.bindListNode.bind(this)
		this.updateSuggestions = debounce(this.updateSuggestions.bind(this), 200)
		this.limit = this.props.limit ? parseInt(this.props.limit) : false

		this.suggestionNodes = []

		this.state = {
			users: [],
			showSuggestions: false,
			selectedSuggestion: null,
			input: ''
		}
	}

	componentDidUpdate() {}

	componentWillUnmount() {
		delete this.suggestionsRequest
	}

	bindListNode(ref) {
		this.listNode = ref
	}

	bindSuggestionNode(index) {
		return ref => {
			this.suggestionNodes[index] = ref
		}
	}

	updateSuggestions(value) {
		// Show the suggestions after typing at least 2 characters
		// and also for URLs
		if ( value.length < 2 || /^https?:/.test(value) ) {
			this.setState({
				showSuggestions: false,
				selectedSuggestion: null,
				loading: false,
			})

			return
		}

		this.setState( {
			showSuggestions: true,
			selectedSuggestion: null,
			loading: true
		} )

		const request = apiFetch( {
			path: addQueryArgs('/wp/v2/users', {
				search: value,
				per_page: 20,
				roles: this.props.roles ? this.props.roles : '',
			})
		} )

		request
			.then( users => {
				// A fetch Promise doesn't have an abort option. It's mimicked by
				// comparing the request reference in on the instance, which is
				// reset or deleted on subsequent requests or unmounting.
				if (this.suggestionsRequest !== request) {
					return
				}

				this.setState({
					users,
					loading: false
				})
			} )
			.catch( () => {
				if ( this.suggestionsRequest === request ) {
					this.setState({
						loading: false
					})
				}
			} )

		this.suggestionsRequest = request
	}

	onChange( event ) {
		const inputValue = event.target.value
		this.setState({ selectedSuggestion: null })
		this.setState({ input: inputValue })
		this.updateSuggestions( inputValue )
	}

	onKeyDown( event ) {
		const {
			showSuggestions,
			selectedSuggestion,
			users,
			loading
		} = this.state
		// If the suggestions are not shown or loading, we shouldn't handle the arrow keys
		// We shouldn't preventDefault to allow block arrow keys navigation
		if ( ! showSuggestions || ! users.length || loading) {
			return
		}

		switch ( event.keyCode ) {
			case UP:
				{
					event.stopPropagation()
					event.preventDefault()
					const previousIndex = !selectedSuggestion ? users.length - 1 : selectedSuggestion - 1
					this.setState({
						selectedSuggestion: previousIndex
					})
					break
				}
			case DOWN:
				{
					event.stopPropagation()
					event.preventDefault()
					const nextIndex = selectedSuggestion === null || selectedSuggestion === users.length - 1 ? 0 : selectedSuggestion + 1
					this.setState({
						selectedSuggestion: nextIndex
					})
					break
				}
			case ENTER:
				{
					if (this.state.selectedSuggestion !== null) {
						event.stopPropagation()
						const user = this.state.users[this.state.selectedSuggestion]
						this.selectLink(user)
					}
				}
		}
	}

	selectLink( user ) {
		// get the "full" user data if a user was selected. this may be something to add as a prop in the future for custom use cases.
		if ( this.props.data ) {
			// if data already exists in the user object, there's no need to make an API call.
			let reachOutToApi = false
			const returnData = {}
			for ( const prop of this.props.data ) {
				if ( ! user.hasOwnProperty( prop ) ) {
					reachOutToApi = true
					return
				}
				returnData[prop] = user[prop]
			}

			if ( ! reachOutToApi ) {
				this.props.onUserSelect( returnData )
				this.setState({
					input: '',
					selectedSuggestion: null,
					showSuggestions: false
				})
				return
			}
		}

		apiFetch( {
			path: `/wp/v2/users/${user.id}`
		} ).then( response => {
			const selectedUser = { ...response }
			// send data to the block;
			this.props.onUserSelect( selectedUser )
		} )

		this.setState( {
			input: '',
			selectedSuggestion: null,
			showSuggestions: false
		} )
	}

	renderSelectedPosts() {
		// show each user in the list.
		return (
			<ul>
				{ this.props.users.map( (user, i) => (
					<li
						style={ { 
							display: 'flex',
							justifyContent: 'flex-start',
							alignItems: 'center',
							flexWrap: 'nowrap'
						} }
						key={ user.id }
					>
						<span 
							style={ {
								flex: 1
							} }
						>
							{ user.name }
						</span>
						<span>
							{ i !== 0 ? (
								<IconButton
									style={ {
										display: 'inline-flex',
										padding: '8px 2px',
										textAlign: 'center'
									} }
									icon="arrow-up-alt2"
									onClick={ () => {
										this.props.users.splice(i - 1, 0, this.props.users.splice(i, 1)[0])
										this.props.onChange(this.props.users)
										this.setState({ state: this.state })
									} }
								/>
							) : null }

							{ i !== this.props.users.length - 1 ? (
								<IconButton
									style={ {
										display: 'inline-flex',
										padding: '8px 2px',
										textAlign: 'center'
									} }
									icon="arrow-down-alt2"
									onClick={ () => {
										this.props.users.splice(i + 1, 0, this.props.users.splice(i, 1)[0])
										this.props.onChange(this.props.users)
										this.setState({ state: this.state })
									} }
								/>
							) : null }

							<IconButton
								style={ {
									display: 'inline-flex',
									textAlign: 'center'
								} }
								icon="no"
								onClick={ () => {
									this.props.users.splice(i, 1)
									this.props.onChange(this.props.users)
									// force a re-render.
									this.setState({ state: this.state })
								} }
							/>
						</span>
					</li>
				) ) }
			</ul>
		)
	}

	render() {
		const {
			autoFocus = true,
			instanceId,
			limit
		} = this.props
		const {
			showSuggestions,
			users,
			selectedSuggestion,
			loading,
			input
		} = this.state
		const inputDisabled = !!limit && this.props.users.length >= limit
		/* eslint-disable jsx-a11y/no-autofocus */
		return (
			<Fragment>
				{ this.renderSelectedPosts() }
				<div className="block-editor-url-input">
					<input
						autoFocus={ autoFocus }
						type="text"
						aria-label={ __( 'URL', i18n ) }
						required
						value={ input }
						onChange={ this.onChange }
						onInput={ stopEventPropagation }
						placeholder={ inputDisabled ? sprintf( __( 'Limted to %s users', i18n ), limit ) : __( 'Type user name', i18n ) }
						onKeyDown={ this.onKeyDown }
						role="combobox"
						aria-expanded={ showSuggestions }
						aria-autocomplete="list"
						aria-owns={ `block-editor-url-input-suggestions-${instanceId}` }
						aria-activedescendant={ selectedSuggestion !== null ? `block-editor-url-input-suggestion-${instanceId}-${selectedSuggestion}` : undefined }
						style={ { width: '100%' } }
						disabled={ inputDisabled }
					/>
					{ loading && <Spinner /> }
				</div>
				{ showSuggestions && !!users.length && (
					<Popover position="bottom" noArrow focusOnMount={ false }>
						<div className="block-editor-url-input__suggestions" id={ `block-editor-url-input-suggestions-${instanceId}` } ref={ this.bindListNode } role="listbox">
							{ users.map( ( user, index ) => (
								<button
									key={ user.id }
									role="option"
									tabIndex="-1"
									id={ `block-editor-url-input-suggestion-${instanceId}-${index}` }
									ref={ this.bindSuggestionNode(index) }
									className={ `block-editor-url-input__suggestion ${index === selectedSuggestion ? 'is-selected' : ''}` }
									onClick={ () => this.selectLink(user) }
									aria-selected={ index === selectedSuggestion }
								>
									<div style={ { display: 'flex', alignItems: 'center' } }>
										<div>{ user.name || __(  'no name', i18n ) }</div>
									</div>
								</button>
							) ) }
						</div>
					</Popover>
				) }
			</Fragment>
		)
		/* eslint-enable jsx-a11y/no-autofocus */
	}
}

export default withSelect( select => {
	const { getEntityRecords } = select('core')
	return {
		sourcePostTypes: getEntityRecords( 'root', 'user', { per_page: -1 } )
	}
} ) ( withInstanceId( UserSelector ) )
