/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n'
import { applyFilters, addFilter } from '@wordpress/hooks'
import { compose } from '@wordpress/compose'
import { Fragment } from '@wordpress/element'
import { i18n } from 'cartzillagb'
import {
	PanelBody, ToggleControl, RangeControl, BaseControl 
} from '@wordpress/components'
import {
	RichText
} from '@wordpress/block-editor'

/**
 * Internal dependencies
 */
import { name } from './index'
import { range } from '~cartzillagb/util'

/**
 * External dependencies
 */
import {
	IconControl, IconDisplay, URLInputControl, ColumnControl, BackgroundSettings, ColorSettings
} from '~cartzillagb/components'
import { withTabbedInspector } from '~cartzillagb/higher-order'
import classnames from 'classnames'

addFilter( 'cartzillagb.info-card.edit.inspector.style.before', 'czgb/info-card', ( output, props ) => {
	const { 
		attributes, 
		setAttributes,  
	} = props

	const {
		enableTitle,
		enableSubtitle,
		enableIcon,
		limit,
		lgColumns,
		mdColumns,
	} = props.attributes

	return (
		<Fragment>
			<PanelBody title={ __( 'General', i18n ) }>
				<RangeControl
					label={ __( 'Number of Blocks', i18n ) }
					value={ limit !== undefined ? limit : 3 }
					onChange={ limit => setAttributes( { limit } ) }
					min={ 1 }
					max={ applyFilters( 'cartzillagb.info-card.limit.edit.inspector.max.limit', 12 ) }
				/>
				<ColumnControl
					lgColumns={ lgColumns }
					mdColumns={ mdColumns }
					onChangelg={ value => setAttributes( { lgColumns: value } ) }
					onChangemd={ value => setAttributes( { mdColumns: value } ) }
					lgMin={ 1 }
					lgMax={ 6 }
					mdMin={ 1 }
					mdMax={ 6 }
				/>
			</PanelBody>
			<PanelBody title={ __( 'Icon Settings', i18n ) }>
				<div className="icon-control-componet-wrapper">
					{ range( 1, limit + 1 ).map( i => {

						let defaultIcon = ''

						if ( (i % 2 ) === 1 ) { 
							defaultIcon = 'czi-edit'
						}

						if ( (i % 2 ) === 0 ) {
							defaultIcon ='czi-instagram'
						}

						const icon = attributes[ `icon${ i }` ]

						return (
							<Fragment key={ i }>
								<IconControl
									label={ __( `Icon #${ i }`, i18n ) }
									value={ icon !== undefined ? icon : defaultIcon }
									onChange={ icon => setAttributes( { [ `icon${ i }` ]: icon } ) }
								/>
							</Fragment>
						)

					} ) }
				</div>
			</PanelBody>
			{ range( 1, limit + 1 ).map( i => {

				const titleColor = attributes[ `titlecolor${ i }` ]
				const customtitlecolor = attributes[ `customtitlecolor${ i }` ]
				const subtitleColor = attributes[ `subtitlecolor${ i }` ]
				const customsubtitlecolor = attributes[ `customsubtitlecolor${ i }` ]
				const bgColor = attributes[ `bgcolor${ i }` ]
				const bgcustomColor = attributes[ `bgcustomcolor${ i }` ]
				return (
					<Fragment key={ i }>
						<PanelBody title={ __( `Color #${ i } Settings`, i18n ) } initialOpen={ false }>
							<BaseControl label={ __( 'Title color', i18n ) } id={ "color-1" } >
								<ColorSettings
									textColor={ titleColor !== undefined ? titleColor : 'text-primary' }
									textcustomColor={ customtitlecolor }
									onChangetextColor={ titleColor => setAttributes( { [ `titlecolor${ i }` ]: titleColor } ) }
									onChangecustomColor={ customtitlecolor => setAttributes( { [ `customtitlecolor${ i }` ]: customtitlecolor.hex } ) }
									onRemovetextColor={ () => { setAttributes( { [ `titlecolor${ i }` ]: '', [ `customtitlecolor${ i }` ]: '' } ) } }
									>
								</ColorSettings>
							</BaseControl>
							<BaseControl label={ __( 'Subtitle color', i18n ) } id={ "color-2" } >
								<ColorSettings
									textColor={ subtitleColor !== undefined ? subtitleColor : 'text-black-50' }
									textcustomColor={ customsubtitlecolor }
									onChangetextColor={ subtitleColor => setAttributes( { [ `subtitlecolor${ i }` ]: subtitleColor } ) }
									onChangecustomColor={ customsubtitlecolor => setAttributes( { [ `customsubtitlecolor${ i }` ]: customsubtitlecolor.hex } ) }
									onRemovetextColor={ () => { setAttributes( { [ `subtitlecolor${ i }` ]: '', [ `customsubtitlecolor${ i }` ]: '' } ) } }
									>
								</ColorSettings>
							</BaseControl>
							<BackgroundSettings
								background={ bgColor !== undefined ? bgColor : 'bg-primary' }
								backgroundColor={ bgcustomColor }
								onChangeBackground={ bgColor => setAttributes( { [ `bgcolor${ i }` ]: bgColor, [ `bgcustomcolor${ i }` ]: '' } ) }
								onChangeBackgroundColor={ bgcustomColor => setAttributes( { [ `bgcustomcolor${ i }` ]: bgcustomColor.hex, [ `bgcolor${ i }` ]: '' } ) }
								onRemoveBackground={ () => { setAttributes( { [ `bgcolor${ i }` ]: '', [ `bgcustomcolor${ i }` ]: '' } ) } }
							/>
						</PanelBody>
					</Fragment>
				)
			} ) }
			<PanelBody title={ __( 'Display Settings', i18n ) }>
				<ToggleControl
					label={ __( 'Enable Title', i18n ) }
					checked={ enableTitle }
					onChange={ enableTitle => setAttributes( { enableTitle } ) }
				/>
				<ToggleControl
					label={ __( 'Enable Description', i18n ) }
					checked={ enableSubtitle }
					onChange={ enableSubtitle => setAttributes( { enableSubtitle } ) }
				/>
				<ToggleControl
					label={ __( 'Enable Icon', i18n ) }
					checked={ enableIcon }
					onChange={ enableIcon => setAttributes( { enableIcon } ) }
				/>
			</PanelBody>
		</Fragment>
	)
} )

const edit = props => {
	const { 
		attributes, 
		setAttributes, 
		className
	} = props 

	const {
		enableTitle,
		enableSubtitle,
		enableIcon,
		limit,
		lgColumns,
		mdColumns,
	} = attributes

	const mainClasses = classnames( [
		className,
		'czgb-info-card',
	], applyFilters( `cartzillagb.${ name.replace( 'czgb/', '' ) }.main-block.classes`, {}, props ) )

	const content = range( 1, ( limit ) + 1 ).map( i => {

		const Title = attributes[ `title${ i }` ]
		const subTitle = attributes[ `subtitle${ i }` ]
		const Icon = attributes[ `icon${ i }` ]
		const Link = attributes[ `link${ i }` ] !== undefined ? attributes[ `link${ i }` ]  : '#'
		const titleColor = attributes[ `titlecolor${ i }` ]
		const customtitlecolor = attributes[ `customtitlecolor${ i }` ]
		const subtitleColor = attributes[ `subtitlecolor${ i }` ]
		const customsubtitlecolor = attributes[ `customsubtitlecolor${ i }` ]
		const bgColor = attributes[ `bgcolor${ i }` ]
		const bgcustomColor = attributes[ `bgcustomcolor${ i }` ]

		const LargeScreen = 12 / lgColumns
		const MediumScreen = 12 / mdColumns

		let defaulttitle = ''
		let defaultdesc = ''
		let defaultIcon = ''

		if ( (i % 2 ) === 1 ) {
			defaulttitle = "Read the blog"
			defaultdesc = "Latest store, fashion news and trends."
			defaultIcon = 'czi-edit'
		}

		if ( (i % 2 ) === 0 ) { 
			defaulttitle = "Follow on Instagram"
			defaultdesc = "#ShopWithCartzilla"
			defaultIcon ='czi-instagram'
		}

		const bgstyle = { backgroundColor: bgcustomColor && bgcustomColor }

		const titleComp = enableTitle && (
			<RichText
				tagName="h5"
				className={ `h5 mb-1 ${ titleColor ? titleColor : customtitlecolor ? '' : 'text-dark' }` }
				value={ Title || defaulttitle }
				style={ { color: customtitlecolor } }
				onChange={ Title => setAttributes( { [ `title${ i }` ]: Title } ) }
				placeholder={ __( 'Title', i18n ) }
				keepPlaceholderOnFocus
			/>
		)

		const subtitleComp = enableSubtitle &&  (
			<RichText
				tagName="p"
				value={ subTitle || defaultdesc }
				className={ `font-size-sm ${ subtitleColor ? subtitleColor : customsubtitlecolor ? '' : 'text-muted' }` }
				style={ { color: customsubtitlecolor } }
				onChange={ subTitle => setAttributes( { [ `subtitle${ i }` ]: subTitle } ) }
				placeholder={ __( 'subtitle', i18n ) }
				keepPlaceholderOnFocus
			/>
		)

		const iconComp = enableIcon && (
			<IconDisplay 
				value={ Icon !== undefined ? Icon : defaultIcon }
				additionalClass="h3 mt-2 mb-4 text-primary" 
			/>    
		)

		const cardLinkComp = (
			 <URLInputControl
				key={ i }
				value={ Link }
				onChange={ Link => setAttributes( { [ `link${ i }` ]: Link } ) }
			/>
		)

		const output = (
			<div className={ `col-md-${ MediumScreen } col-lg-${ LargeScreen } info-card` }>
				<button className={ `d-flex border-0 rounded-0 text-decoration-none py-md-4 h-100 align-items-center w-100 ${ bgColor ? bgColor : bgcustomColor ? '' : ( i === 1 ? 'bg-faded-primary' : 'bg-faded-accent' ) }` } style={ bgstyle } href='#'>
					<div className="card-body text-center">
						{ iconComp }
						{ titleComp }
						{ subtitleComp }
					</div>
				</button>
				{ cardLinkComp }
			</div>
		)
			
			return output
	}   )

	return (
		<div className={ mainClasses }>
			<section className="container-fluid px-0">
				<div className="row no-gutters">
					{ content }
				</div>
			</section>
		</div>
	)
}

export default compose(
	withTabbedInspector( [ 'style', 'advanced' ] ),
)( edit )
