/**
 * External dependencies
 */
import { Component } from 'react'
import classnames from 'classnames'

import { range } from '~cartzillagb/util'

class ProductsListPlaceholder extends Component {

	render() {
		const {
			limit = 3,
		} = this.props

		const output = (
			<ul className={ classnames( [
				'row',
				'list-unstyled',
				'products',
				'products-group',
				'no-gutters',
				'mb-0',
				'w-100',
				'columns-1',
			], {} ) }>
				{ range( 1, limit + 1 ).map( i => {
					return(
						<li className="product cartzilla-product-list-placeholder" key={ `product-${i}` }>
							<div className="media product-card">
								<div className="card-img-top d-block overflow-hidden position-relative mr-2" style={ { width: '64px', height:'64px' } }>
									<div className="iframe-full-height bg-muted"></div>
								</div>
								<div className="media-body py-2">
									<h6 className="widget-product-title product-title font-size-sm progress w-50 mb-3">
									</h6>
									<div className="widget-product-meta d-flex justify-content-between progress w-25">
									</div>
								</div>
							</div>
						</li>
					)
				} ) }
			</ul>
		)

		return output
	}
}

export default ProductsListPlaceholder