/**
 * External dependencies
 */
import { __ } from '@wordpress/i18n'
import { SelectControl } from '@wordpress/components'
import PropTypes from 'prop-types'

/**
 * A pre-configured SelectControl for product orderby settings.
 */
const ProductOrderbyControl = ( { value, setAttributes } ) => {
	return (
		<SelectControl
			label={ __( 'Order products by', 'cartzilla' ) }
			value={ value }
			options={ [
				{
					label: __(
						'Newness - newest first',
						'cartzilla'
					),
					value: 'date',
				},
				{
					label: __(
						'Price - low to high',
						'cartzilla'
					),
					value: 'price_asc',
				},
				{
					label: __(
						'Price - high to low',
						'cartzilla'
					),
					value: 'price_desc',
				},
				{
					label: __(
						'Rating - highest first',
						'cartzilla'
					),
					value: 'rating',
				},
				{
					label: __(
						'Sales - most first',
						'cartzilla'
					),
					value: 'popularity',
				},
				{
					label: __(
						'Title - alphabetical',
						'cartzilla'
					),
					value: 'title',
				},
				{
					label: __( 'Menu Order', 'cartzilla' ),
					value: 'menu_order',
				},
			] }
			onChange={ orderby => setAttributes( { orderby } ) }
		/>
	)
}

ProductOrderbyControl.propTypes = {

	/**
	 * Callback to update the order setting.
	 */
	setAttributes: PropTypes.func.isRequired,

	/**
	 * The selected order setting.
	 */
	value: PropTypes.string.isRequired,
}

export default ProductOrderbyControl
