/**
 * External dependencies
 */
import { HeaderIcon } from '~cartzillagb/icons'
import { range, createButtonAttributes } from '~cartzillagb/util'
import {
    disabledBlocks, i18n 
} from 'cartzillagb'

/**
 * Internal dependencies
 */
import save from './save'
import edit from './edit'


/**
 * WordPress dependencies
 */
import { applyFilters } from '@wordpress/hooks'
import { __ } from '@wordpress/i18n'

export const schema = {
    blockId: {
        type: 'string',
        default: '',
    },

    limit: {
        type: 'number',
        default: 1,
    },

    enableButton: {
        type: 'boolean',
        default: true,
    },

    ...createButtonAttributes( 'button1%s', { 
        defaultText: 'Shop Now',
        iconAfterText: true,
        defaultIcon: 'czi-arrow-right',
        defaultAnimation: 'scale-up',
        defaultDelay: 'delay-4' 
    } ),
    ...createButtonAttributes( 'button2%s', { 
        defaultText: 'Shop Now',
        iconAfterText: true,
        defaultIcon: 'czi-arrow-right',
        defaultAnimation: 'scale-up',
        defaultDelay: 'delay-4' 
    } ),
    ...createButtonAttributes( 'button3%s', { 
        defaultText: 'Shop Now',
        iconAfterText: true,
        defaultIcon: 'czi-arrow-right',
        defaultAnimation: 'scale-up',
        defaultDelay: 'delay-4' 
    } ),
    ...createButtonAttributes( 'button4%s', { 
        defaultText: 'Shop Now',
        iconAfterText: true,
        defaultIcon: 'czi-arrow-right',
        defaultAnimation: 'scale-up',
        defaultDelay: 'delay-4' 
    } ),
    ...createButtonAttributes( 'button5%s', { 
        defaultText: 'Shop Now',
        iconAfterText: true,
        defaultIcon: 'czi-arrow-right',
        defaultAnimation: 'scale-up',
        defaultDelay: 'delay-4' 
    } ),
}

const Maxlimit = applyFilters( 'cartzillagb.hero-carousel.edit.inspector.max.limit', 5 )

{ range( 1, Maxlimit + 1 ).map( i => {
    schema[ `pretitle${ i }` ] = {
        type: 'string',
    }
    schema[ `title${ i }` ] = {
        type: 'string',
    }
    schema[ `subtitle${ i }` ] = {
        type: 'string',
    }
    schema[ `backgroundImage${ i }` ] = {
        type: 'string',
    }
    schema[ `bgcolor${ i }` ] = {
        type: 'string',
    }
    schema[ `bgcustomcolor${ i }` ] = {
        type: 'string',
    }
    schema[ `mediaURL${ i }` ] = {
        type: 'string',
    }
    schema[ `enablepreTitle${ i }` ] = {
        type: 'boolean',
        default: true,
    }
    schema[ `enableTitle${ i }` ] = {
        type: 'boolean',
        default: true,
    }
    schema[ `enablesubTitle${ i }` ] = {
        type: 'boolean',
        default: true,
    }
    schema[ `pretitleAnimation${ i }` ] = {
        type: 'string',
        default: 'none'
    }
    schema[ `pretitleDelay${ i }` ] = {
        type: 'string',
        default: 'none'
    }
    schema[ `titleAnimation${ i }` ] = {
        type: 'string',
        default: 'none'
    }
    schema[ `titleDelay${ i }` ] = {
        type: 'string',
        default: 'none'
    }
    schema[ `subtitleAnimation${ i }` ] = {
        type: 'string',
        default: 'from-bottom'
    }
    schema[ `subtitleDelay${ i }` ] = {
        type: 'string',
        default: 'delay-2'
    }
    return ""
} ) }

export const name = 'czgb/hero-carousel'

export const settings = {
    title: __( 'Hero Carousel #1', i18n ),
    description: __( 'Start building your website with Hero carousel #1.', i18n ),
    icon: HeaderIcon,
    category: 'cartzillagb',
    example: {},
    keywords: [
        __( 'Hero Carousel', i18n ),
        __( 'CartzillaGb', i18n ),
    ],
    attributes: schema,
    supports: {
        inserter: ! disabledBlocks.includes( name ), // Hide if disabled.
    },

    edit,
    save,

    // CartzillaGB modules.
    modules: {
        'advanced-responsive': true,
        'custom-css': {
            default: applyFilters( 'cartzillagb.hero-carousel.custom-css.default', '' ),
        },
    },
}