/**
 * External dependencies
 */
import { Component } from 'react'
import classnames from 'classnames'

import { range } from '~cartzillagb/util'

class ProductsPlaceholder extends Component {

	render() {
		const {
			columns = 3,
			limit = 3,
			placeholder = 0,
		} = this.props

		const output = (
			<ul className={ classnames( [
				'row',
				'list-unstyled',
				'products',
				'products-group',
				'no-gutters',
				'mb-0',
				'w-100',
				`columns-${columns}`,
			], {} ) }>
				{ range( 1, ( placeholder > 0 ? placeholder : limit ) + 1 ).map( i => {
					return(
						<li className="product cartzilla-product-placeholder" key={ `product-${i}` }>
							<div className="cartzilla-product-grid">
								<div className="card product-card">
									<div className="badge badge-shadow w-25">
										<div className="progress bg-light"></div>
									</div>
									<div className="card-img-top d-block overflow-hidden w-100 position-relative" style={ { paddingTop:'100%' } }>
										<div className="iframe-full-height bg-muted"></div>
									</div>
									<div className="card-body py-2">
										<div className="woocommerce-loop-product__categories progress w-25 mb-2">
										</div>
										<h3 className="woocommerce-loop-product__title product-title font-size-sm progress w-50 mb-3">
										</h3>
										<div className="d-flex justify-content-between progress w-25">
										</div>
									</div>
								</div>
								<hr className="d-sm-none" />
							</div>
						</li>
					)
				} ) }
			</ul>
		)

		return output
	}
}

export default ProductsPlaceholder