/**
 * External dependencies
 */
import { HeaderIcon } from '~cartzillagb/icons'
import { disabledBlocks, i18n } from 'cartzillagb'

import {
	createButtonAttributes,
	createResponsiveAttributes,
} from '~cartzillagb/util'

/**
 * Internal dependencies
 */
import save from './save'
import edit from './edit'

/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n'
import { applyFilters } from '@wordpress/hooks'

export const schema = {
	enableContainer: {
		type: 'boolean',
		default: true,
	},

	enableHeader: {
		type: 'boolean',
		default: true,
	},

	headerStyle: {
		type: 'string',
		default: 'style-v1',
	},

	// Title.
	showTitle: {
		type: 'boolean',
		default: true,
	},

	title: {
		type: 'string',
		default: __( 'Title for This Block', i18n ),
	},

	titleTag: {
		type: 'string',
	},

	// Header Button.
	showHeaderButton: {
		type: 'boolean',
		default: false,
	},

	...createButtonAttributes( 'headerButton%s', {
		defaultText: 'More products',
		defaultIcon: 'czi-arrow-right',
		defaultSize: 'sm',
		defaultDesign: 'outline',
		backgroundColor: 'accent',
		iconAfterText: true,
	} ),

	// Footer Button.
	showFooterButton: {
		type: 'boolean',
		default: true,
	},

	...createButtonAttributes( 'footerButton%s', {
		defaultText: 'More products',
		defaultIcon: 'czi-arrow-right',
		defaultDesign: 'outline',
		backgroundColor: 'accent',
		iconAfterText: true,
	} ),

	// Spacing.
	...createResponsiveAttributes( 'header%sBottomMargin', {
		type: 'number',
		default: '',
	} ),

	...createResponsiveAttributes( 'footer%sTopMargin', {
		type: 'number',
		default: '',
	} ),
}

export const name = 'czgb/products-with-header'

export const settings = {
	title: __( 'Products Block', i18n ),
	icon: HeaderIcon,
	category: 'cartzillagb',
	example: {},
	keywords: [
		__( 'Products', i18n ),
		__( 'CartzillaGb', i18n ),
	],
	attributes: schema,
	description: __( 'A styled products with header that you can add other blocks inside. Use this to create unique layouts.', i18n ),
	supports: {
		inserter: ! disabledBlocks.includes( name ), // Hide if disabled.
	},

	edit,
	save,

	// CartzillaGB modules.
	modules: {
		'advanced-general': true,
		'advanced-responsive': true,
		'advanced-block-spacing': true,
		'custom-css': {
			default: applyFilters( 'cartzillagb.products-with-header.custom-css.default', '' ),
		},
	},
}