/**
 * Internal dependencies
 */
import ButtonEdit from './index'

/**
 * WordPress dependencies
 */
import { sprintf } from '@wordpress/i18n'

/**
 * External dependencies
 */
import { camelCase } from 'lodash'

const ButtonEditHelper = props => {
	const { setAttributes } = props
	const getAttrName = attrName => camelCase( sprintf( props.attrNameTemplate, attrName ) )
	const getAttrValue = ( attrName, defaultValue = '' ) => {
		const value = props.blockAttributes[ getAttrName( attrName ) ]
		return value === 0 ? value : ( value || defaultValue )
	}

	return (
		<ButtonEdit
			design={ getAttrValue( 'Design', props.designDefault ) }
			url={ getAttrValue( 'Url' ) }
			size={ getAttrValue( 'Size' ) }
			animation={ getAttrValue( 'Animation' ) }
			delay={ getAttrValue( 'Delay' ) }
			text={ getAttrValue( 'Text', '' ) }
			shadow={ getAttrValue( 'Shadow' ) }
			icon={ getAttrValue( 'Icon' ) }
			shape={ getAttrValue( 'Shape' ) }
			buttonIcon={ getAttrValue( 'Button Icon' ) }
			backgroundColor={ getAttrValue( 'BackgroundColor' ) }
			iconAfterText={ getAttrValue( 'IconAfterText' ) }

			onChangeUrl={ value => setAttributes( { [ getAttrName( 'Url' ) ]: value } ) }
			onChangeSize={ value => setAttributes( { [ getAttrName( 'Size' ) ]: value } ) }
			onChangeAnimation={ value => setAttributes( { [ getAttrName( 'Animation' ) ]: value } ) }
			onChangeDelay={ value => setAttributes( { [ getAttrName( 'Delay' ) ]: value } ) }
			onChange={ value => setAttributes( { [ getAttrName( 'Text' ) ]: value } ) }
			onChangeShadow={ value => setAttributes( { [ getAttrName( 'Shadow' ) ]: value } ) }
			onChangeButtonIcon={ value => setAttributes( { [ getAttrName( 'Button Icon' ) ]: value } ) }
			onChangeIcon={ value => setAttributes( { [ getAttrName( 'Icon' ) ]: value } ) }
			onChangeShape={ value => setAttributes( { [ getAttrName( 'Shape' ) ]: value } ) }
			onChangeBackgroundColor={ value => setAttributes( { [ getAttrName( 'BackgroundColor' ) ]: value } ) }
			onChangeiconAfterText={ value => setAttributes( { [ getAttrName( 'IconAfterText' ) ]: value } ) }

			isSelected={ props.isSelected }
			{ ...props }
		/>
	)
}

ButtonEditHelper.defaultProps = {
	attrNameTemplate: '%s',
	setAttributes: () => {},
	blockAttributes: {},
	designDefault: 'solid',
}

ButtonEditHelper.Content = props => {
	const getAttrName = attrName => camelCase( sprintf( props.attrNameTemplate, attrName ) )
	const getAttrValue = ( attrName, defaultValue = '' ) => {
		const value = props.blockAttributes[ getAttrName( attrName ) ]
		return value === 0 ? value : ( value || defaultValue )
	}

	return (
		<ButtonEdit.Content
			design={ getAttrValue( 'Design', props.designDefault ) }
			url={ getAttrValue( 'Url' ) }
			size={ getAttrValue( 'Size' ) }
			animation={ getAttrValue( 'Animation' ) }
			delay={ getAttrValue( 'Delay' ) }
			shape={ getAttrValue( 'Shape' ) }
			text={ getAttrValue( 'Text' ) }
			shadow={ getAttrValue( 'Shadow' ) }
			icon={ getAttrValue( 'Icon' ) }
			buttonIcon={ getAttrValue( 'Button Icon' ) }
			backgroundColor={ getAttrValue( 'BackgroundColor' ) }
			iconAfterText={ getAttrValue( 'IconAfterText' ) }
			{ ...props }
		/>
	)
}

ButtonEditHelper.Content.defaultProps = {
	attrNameTemplate: '%s',
	blockAttributes: {},
	designDefault: 'solid',
}

export default ButtonEditHelper