/**
 * External dependencies
 */
import {
	IconDisplay, ImageUploadPlaceholder
} from '~cartzillagb/components'
import {
	withUniqueClass,
} from '~cartzillagb/higher-order'
import classnames from 'classnames'
import { getBootstrapClasses, range } from '~cartzillagb/util'
import { __ } from '@wordpress/i18n'
import { i18n } from 'cartzillagb'

/**
 * WordPress dependencies
 */
import { RichText } from '@wordpress/block-editor'
import { applyFilters } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { compose } from '@wordpress/compose'


const save = props => {
	const {
		attributes,
	} = props

	const {
		className,
		design,
		enableContainer,
		limit,
		lgColumns,
		mdColumns,
		smColumns,
		xlColumns,
		enableGutter,
		heading,
		backgroundImage,
		background,
		backgroundColor,
		titlecolor,
		desccolor,
		customtitlecolor,
		customdesccolor,
		headingcolor,
		customheadingcolor,
		enableheader,
		iconcolor,
		customiconcolor,
		enableIcon
	} = attributes

	const mainClasses = classnames( [
		className,
		'icon-block',
		( design==='style-2' && background !== undefined ) ? background : ( design==='style-2' && 'bg-accent' ),
		( design ==='style-3' && background !== undefined ) ? background : ( design ==='style-3' && 'bg-darker' ),

	], applyFilters( 'cartzillagb.icon-blocks.main-block.classes', {
		'pt-4 mb-5': design === 'style-1',
		'bg-size-cover bg-position-center pt-5 pb-4 pb-lg-5' : design === 'style-2',
	}, props ) )


	const containerClasses = classnames( [], {
		'container': enableContainer,
		'pt-lg-3': design === 'style-2',
	} ) 

	const rowClasses = classnames( [
		'row',
		( design==='style-1' &&  background !== undefined ) ? background : ( design==='style-1' && 'bg-light' ),
		], {
		'no-gutters':( enableGutter === false && design === 'style-1' ),
		'rounded-lg': design === 'style-1',
		'pt-lg-2 text-center': design === 'style-2',
	} )

	const limitvalue =limit  !== undefined ? limit : ( design ==='style-1' ? 3 : 4 )

	let defaultheading = ''
	let defaultheadingcolor = ''
	let imageClasses ='' 

		if (design === 'style-1') {
			defaultheading='How it works?'
			defaultheadingcolor='text-dark'
		}
		if (design === 'style-2') {
			defaultheading='Why our marketplace?'
			defaultheadingcolor='text-light'
		}
		if (design === 'style-3') {
			defaultheading='Why our marketplace?'
			defaultheadingcolor='text-light'
		}

	const headingComp = (
		<RichText.Content
			tagName='h2'
			className={ `h3 text-center  ${ headingcolor ? headingcolor : customheadingcolor ? '' : defaultheadingcolor } ${ design ==='style-1' ? 'mb-grid-gutter pt-2' : 'mb-3 pb-4'}` }
			value={ heading || defaultheading }
			style={ { color: customheadingcolor } }
		/>
	)

	const htmlAttr = {}

	if( ( backgroundImage || backgroundColor ) && design === 'style-2' ) {
		htmlAttr.style = {
			backgroundImage: "url(" + ( backgroundImage ) + ")",
			backgroundColor: backgroundColor && backgroundColor,

		}
	}

	const styleAttr = {}

	if( backgroundColor && design === 'style-1' ) {
		styleAttr.style = {
			backgroundColor: backgroundColor && backgroundColor,

		}
	}

	if ( design === 'style-1' ) {
		imageClasses = 'd-block my-2';
	} else {
		imageClasses = 'image';
	}


	const content = range( 1, ( limitvalue) + 1 ).map( i => {

		const title = attributes[ `title${ i }` ]
		const desc = attributes[ `desc${ i }` ]
		const Icon = attributes[ `icon${ i }` ]
		const mediaURL = attributes[ `mediaURL${ i }` ]

		let defaulttitle =''
		let defaultdesc =''
		let defaulticon =''
		let defaulticonclass = ''

		if (design === 'style-1') {
			defaulticonclass = `d-block my-2 font-size-72 ${ iconcolor ? iconcolor : customiconcolor ? '' : 'text-dark' }`
			if ( (i % 3 ) === 1 ) {
				defaulttitle = "01"
				defaultdesc = "You order your favorite products online"
				defaulticon='fas fa-shopping-cart' 
			}

			if ( (i % 3 ) === 2 ) {               
				defaulttitle = "02"
				defaultdesc = "A personal assistant collects the products from your list"
				defaulticon='czi-bag' 
			}

			if ( (i % 3 ) === 0 ) {               
				defaulttitle = "03"
				defaultdesc = "We deliver to the door at a time convenient for you"
				defaulticon='fas fa-shuttle-van' 
			}
		}

		if (design === 'style-2') {
			defaulticonclass = `font-size-52 text-white ${ iconcolor ? iconcolor : customiconcolor ? '' : 'text-white' }`

			if ( (i % 4 ) === 1 ) {
				defaulttitle = "Quality Guarantee"
				defaultdesc = "Quality checked by our team"
				defaulticon='fas fa-check' 
			}

			if ( (i % 4 ) === 2 ) {               
				defaulttitle = "Customer Support"
				defaultdesc = "Friendly 24/7 customer support"
				defaulticon='fas fa-headset'
			}

			if ( (i % 4 ) === 3 ) {               
				defaulttitle = "Lifetime Free Updates"
				defaultdesc = "Never pay for an update"
				defaulticon='fas fa-lock'
			}

			if ( (i % 4 ) === 0 ) {               
				defaulttitle = "Secure Payments"
				defaultdesc = "we posess SSL / Secure сertificate"
				defaulticon='fas fa-random'
			}
		}

		if (design === 'style-3') {
			defaulticonclass = `text-primary font-size-36 ${ iconcolor ? iconcolor : customiconcolor ? '' : 'text-primary' }`

			if ( (i % 4 ) === 1 ) {
				defaulttitle = "Fast and free delivery"
				defaultdesc = "Free delivery for all orders over $200"
				defaulticon='czi-rocket' 
			}

			if ( (i % 4 ) === 2 ) {               
				defaulttitle = "Money back guarantee"
				defaultdesc = "We return money within 30 days"
				defaulticon='czi-currency-exchange'
			}

			if ( (i % 4 ) === 3 ) {               
				defaulttitle = "24/7 customer support"
				defaultdesc = "Friendly 24/7 customer support"
				defaulticon='czi-support'
			}

			if ( (i % 4 ) === 0 ) {               
				defaulttitle = "Secure online payment"
				defaultdesc = "We possess SSL / Secure сertificate"
				defaulticon='czi-card'
			}
		}

		
		const iconComp = (
			<IconDisplay 
				value={ Icon || defaulticon }
				additionalClass={ defaulticonclass }
				style={ { color: customiconcolor } }
			/>    
		)

		const imageComp = (
			<ImageUploadPlaceholder
				imageURL={ mediaURL }
				render={ <img src={ mediaURL } className={ imageClasses } width={ design === 'style-1' ? '72' : '52' } alt={ __( 'Image', i18n ) } /> }
			/>
		)

		let titleClass= ''

		if ( design === 'style-1' ) {
			titleClass = 'display-2 font-weight-normal border-color mr-4 text-nowrap'
		} else {
			titleClass = `font-size-base mb-1 ${ titlecolor ? titlecolor : customtitlecolor ? '' : 'text-light' }`
		}

		let descClass= ''

		if ( design === 'style-1' ) {
			descClass = `mb-3 pt-1 ${ desccolor ? desccolor : customdesccolor ? '' : 'text-dark' }`
		} else if ( design === 'style-2' ) {
			descClass =`font-size-ms opacity-70 mb-0 ${ desccolor ? desccolor : customdesccolor ? '' : 'text-light' }`
		} else {
			descClass=`mb-0 font-size-ms opacity-50 ${ desccolor ? desccolor : customdesccolor ? '' : 'text-light' }`
		}


		const titleComp = (
			<RichText.Content
				tagName={ design === 'style-1' ? 'h6' : 'div' }
				className={ titleClass }
				value={ title || defaulttitle }
				style={ { color: customtitlecolor } }
			/>
		)

		const descComp = (
			<RichText.Content
				tagName='p'
				className={ descClass }
				value={ desc || defaultdesc }
				style={ { color: customdesccolor } }
			/>
		)

		const bootstrapClass = getBootstrapClasses( undefined, undefined, mdColumns, lgColumns !== undefined ? lgColumns : 1, xlColumns )

		const bootstrapClass2 = getBootstrapClasses( undefined, smColumns, undefined, lgColumns !== undefined ? lgColumns : 4, undefined )

		const bootstrapClass3 = getBootstrapClasses( undefined, smColumns, mdColumns !== undefined ? mdColumns : 4, undefined )


		let style = ''

		if ( design === 'style-1' ) {
			style = (
				<div className={ classnames( [ `${ bootstrapClass } ${ i !== ( limitvalue ) ? 'border-right' : ''}` ] ) }>
					<div className="py-3">
						<div className="d-flex align-items-center mx-auto py-3 px-3 mw-22">
							{ titleComp }
							<div className="pl-2">
								{ enableIcon && 
									iconComp
								}

								{ ! enableIcon && 
									imageComp
								}
								{ descComp }
							</div>
						</div>
						<hr className="d-md-none d-lg-block d-xl-none" />
					</div>
				</div>
			)
		}

		if ( design === 'style-2' ) {
			style = (
				<div className={ `${ bootstrapClass2 } mb-grid-gutter` }  key={ i }>
					<div className="d-inline-block">
						<div className="media media-ie-fix align-items-center text-left">
							{ enableIcon && 
								iconComp
							}

							{ ! enableIcon && 
								imageComp
							}
							<div className="media-body pl-3">
								{ titleComp }
								{ descComp }
							</div>
						</div>
					</div>
				</div>
			)
		}

		if ( design === 'style-3' ) {
			style = (
				<div className={ `${ bootstrapClass3 } mb-4` }  key={ i }>
					<div className="media">
						{ enableIcon && 
							iconComp
						}

						{ ! enableIcon && 
							imageComp
						}
						<div className="media-body pl-3">
							{ titleComp }
							{ descComp }
						</div>
					</div>
				</div>
			)
		}

		return style
	} )

	return (
		<Fragment>
			{ design !== 'style-3' &&
				<section className={ mainClasses } { ...htmlAttr }>
					<div className={ containerClasses }>
						<Fragment>
							{ enableheader &&
								headingComp
							}
							<div className={ rowClasses } { ...styleAttr }>
								{ content }
							</div>
						</Fragment>
					</div>
				</section>
			}
			{ design === 'style-3' &&
				<div className={ mainClasses }>
					<div className={ containerClasses }>
						{ enableheader &&
							headingComp
						}
						<div className="row pb-3">
							{ content }
						</div>
					</div>
				</div>
			}
		</Fragment>
	)  
}


export default compose(
	withUniqueClass,
)( save )