/**
 * External dependencies
 */
import {
	ImageUploadPlaceholder, IconControl, IconDisplay, ColorSettings
} from '~cartzillagb/components'
import {
	withTabbedInspector,
} from '~cartzillagb/higher-order'
import classnames from 'classnames'
import { range } from '~cartzillagb/util'
import { i18n } from 'cartzillagb'

/**
 * WordPress dependencies
 */
import {
	PanelBody, ToggleControl, RangeControl, BaseControl
} from '@wordpress/components'
import { __ } from '@wordpress/i18n'
import { RichText } from '@wordpress/block-editor'
import { addFilter, applyFilters } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { compose } from '@wordpress/compose'

addFilter( 'cartzillagb.info-section-1.edit.inspector.style.before', 'cartzillagb/info-section-1', ( output, props ) => {
	const { attributes, setAttributes } = props
	const {
		enableContainer,
		displayTitle,
		displayDesc,
		displayList,
		displayImage,
		limit,
		iconcolor,
		enableIcon
	} = attributes

	let defaulticoncolor = ''

	defaulticoncolor='text-success'

	return (
		<Fragment>
            <PanelBody title={ __( 'General Settings', i18n ) } initialOpen={ true }>
                <ToggleControl
                    label={ __( 'Enable Container', i18n ) }
                    checked={ enableContainer }
                    onChange={ enableContainer => setAttributes( { enableContainer } ) }
                />
                
                <ToggleControl
                    label={ __( 'Display Title', i18n ) }
                    checked={ displayTitle }
                    onChange={ displayTitle => setAttributes( { displayTitle } ) }
                />

                <ToggleControl
                    label={ __( 'Display Description', i18n ) }
                    checked={ displayDesc }
                    onChange={ displayDesc => setAttributes( { displayDesc } ) }
                />

                <ToggleControl
                    label={ __( 'Display List Items', i18n ) }
                    checked={ displayList }
                    onChange={ displayList => setAttributes( { displayList } ) }
                />
                { displayList && 
	                <ToggleControl
	                    label={ __( 'Display Icon', i18n ) }
	                    checked={ enableIcon }
	                    onChange={ enableIcon => setAttributes( { enableIcon } ) }
	                />
	            }
                
                <ToggleControl
                    label={ __( 'Display Image', i18n ) }
                    checked={ displayImage }
                    onChange={ displayImage => setAttributes( { displayImage } ) }
                />

                { displayList === true && 
                	<Fragment>
                        <RangeControl
                            label={ __( 'List Limit', i18n ) }
                            value={ limit }
                            onChange={ limit => setAttributes( { limit } ) }
                            min={ 1 }
                            max={ applyFilters( 'cartzillagb.info-section-1.edit.inspector.max.limit', 10 ) }
                        />

                        { enableIcon && 
	                        <BaseControl label={ __( 'Icon color', i18n ) } id={ "icon" }>
								<ColorSettings
									textColor={ iconcolor || defaulticoncolor }
									onChangetextColor={ iconcolor => setAttributes( { iconcolor } ) }
									onRemovetextColor={ () => { setAttributes( { iconcolor: '' } ) } }
								>
								</ColorSettings>
							</BaseControl>
						}
					</Fragment>
				}

            </PanelBody>
                
	
			{ ( displayList && enableIcon ) &&
				<PanelBody title={ __( 'Icon Settings', i18n ) }>
					<div className="icon-control-componet-wrapper">
						{ range( 1, limit + 1 ).map( i => {
							const defaulticon ='czi-eye'
							

							const Icon = attributes[ `icon${ i }` ]

							return (
								<IconControl 
									key={ i }
									label={ __( `Icon #${ i }`, i18n ) }
									value={ Icon || defaulticon }
									onChange={ Icon => setAttributes( { [ `icon${ i }` ]: Icon } ) }
								/>
							)
						} ) }
					</div>
				</PanelBody>
			}
		</Fragment>
		
	)
} )

const edit = props => {
	const {
		setAttributes, className, attributes,
	} = props

	const {
		enableContainer,
		displayTitle,
		displayDesc,
		displayList,
		displayImage,
		title,
		subtitle,
		limit,
        customiconcolor,
		iconcolor,
		mediaURL,
		enableIcon
	} = attributes

	const mainClasses = classnames( [
		className,
		'bg-dark py-5',
		'info-section-1-block'
	], applyFilters( 'cartzillagb.info-section-1.main-block.classes', {}, props ) )

	const containerClasses = classnames( [
		' py-3 py-lg-4',
		enableContainer ? 'container' : '',
	] )
		

	return (
		<section className={ mainClasses }>
			<div className={ containerClasses }>
				<div className="row align-items-center">
					<div className="col-xl-4 col-lg-5 pb-4 pb-lg-0">
						{ displayTitle && 
							<RichText
								tagName='h2'
								className="text-light"
								value={ title || '<span class="font-weight-light">Complete</span> E-Commerce Front-End <span class="font-weight-light">Solution</span>' }
								onChange={ title => setAttributes( { title } ) }
								placeholder={ __( 'title' ) }
								keepPlaceholderOnFocus
							/>
						}

						{ displayDesc && 
							<RichText
								tagName='p'
								className="text-light opacity-70"
								value={ subtitle || 'All you need for your next e-commerce project' }
								onChange={ subtitle => setAttributes( { subtitle } ) }
								placeholder={ __( 'subtitle' ) }
								keepPlaceholderOnFocus
							/>
						}

						{ displayList && 
							<ul className={ `text-light list-unstyled pt-3 ` }>
			                    { range(1, limit + 1).map(i => {
			                        const Icon = attributes[ `icon${ i }` ]
									const listitems=attributes[ `listitems${ i }` ]

									let defaultListItems =''
									const defaultIcon = 'czi-check-circle'

									if ( (i % 10 ) === 1 ) {               
										defaultListItems = (__('Shop Grid / List Layout Options', i18n )) 
									}
									if ( (i % 10 ) === 2 ) {               
										defaultListItems = (__('Multiple Product Page Variations', i18n ))
									}
									if ( (i % 10 ) === 3 ) {               
										defaultListItems = (__('Complete Order Workflow: Cart + Checkout', i18n ))
									}
									if ( (i % 10 ) === 4 ) {               
										defaultListItems = (__('Shop Customer Account Pages', i18n ))
									}
									if ( (i % 10 ) === 5 ) {               
										defaultListItems = (__('Vendor Dashboard for Multi-vendor Marketplaces', i18n ))
									}
									if ( (i % 10 ) === 6 ) {               
										defaultListItems = (__('Ready for any Content: Fashion, Electronics, Digital (Downloadable) Products, etc.', i18n ))
									}
									if ( (i % 10 ) === 7 ) {               
										defaultListItems = (__('Blog Pages: Blog Layouts + Single Post', i18n ))
									}
									if ( (i % 10 ) === 8 ) {               
										defaultListItems = (__('Help Center / Support Pages', i18n ))
									}
									if ( (i % 10 ) === 9 ) {               
										defaultListItems = (__('Secondary Pages: About, Contacts, 404, etc.', i18n ))
									}
									if ( (i % 10 ) === 0 ) {               
										defaultListItems = (__('B40+ Flexible Components', i18n ))
									}

									const iconComp = (
										<IconDisplay 
											value={ Icon || defaultIcon }
											additionalClass={`mr-2 ${ iconcolor ? iconcolor : customiconcolor ? '' : 'text-success' }`}
										/>    
									)

									const listItemComp = (
										<RichText
											tagName="span"
											value={ listitems || defaultListItems }
											onChange={ listitems => setAttributes( { [ `listitems${ i }` ]: listitems } ) }
											placeholder={ __( 'Listitems' ) }
											keepPlaceholderOnFocus
										/>
									)

									return ( 
										<li className="d-flex align-items-center" key={ i }>
							            	{ enableIcon && iconComp } { listItemComp }
							            </li>
							   
							        ) 
								} ) }
			                </ul>
						}
					</div>
					{ displayImage && 
						<div className="col-lg-7 offset-xl-1">
							<ImageUploadPlaceholder
								imageURL={ mediaURL }
								onRemove={ () => {
									setAttributes( {
										mediaURL: '',
									} )
								} }
								onChange={ ( { url } ) => {
									setAttributes( {
										mediaURL: url,
									} )
								} }
								hasRemove={ false }
								render={ <img src={ mediaURL } alt={ __( 'Cartzilla pages' ) } /> }
							/>
						</div>
					}
				</div>
			</div>
		</section>
	)  
}

export default compose(
	withTabbedInspector( [ 'style', 'advanced' ] ),
)( edit )