/**
 * External dependencies
 */
import { IconListIcon } from '~cartzillagb/icons'
import { range } from '~cartzillagb/util'
import { disabledBlocks, i18n } from 'cartzillagb'

/**
 * Internal dependencies
 */
import save from './save'
import edit from './edit'
import deprecated from './deprecated'


/**
 * WordPress dependencies
 */
import { applyFilters } from '@wordpress/hooks'
import { __ } from '@wordpress/i18n'


export const schema = {
	className : {
		type: 'string',
	},
	enableContainer: {
		type: 'boolean',
		default: true,
	},
	limit: {
		type: 'number',
		default: 4,
	},
	listlimit: {
		type: 'number',
		default: 2,
	},
	lgColumns : {
		type: 'number',
		default: 4,
	},
	mdColumns : {
		type: 'number',
		default: 2,
	},
	smColumns : {
		type: 'number',
		default: 12,
	},
	desc : {
		type: 'string',
	},
	mapicon : {
		type: 'string',
	},
	linktext : {
		type: 'string',
	},
	maplink : {
		type: 'string',
		default: '#',
	},
	list1 : {
		type: 'string',
	},
	list2 : {
		type: 'string',
	},
}

const maxLimit = applyFilters( 'cartzillagb.contact-details.edit.inspector.max.limit', 12 )

{ range( 1, maxLimit + 1 ).forEach( i => {
	schema[ `heading${ i }` ] = {
		type: 'string',
	}
	schema[ `icon${ i }` ] = {
		type: 'string',
	}
	const MaxListlimit = applyFilters( 'cartzillagb.contact-details.edit.inspector.max.listlimit', 6 )

	{ range( 1, MaxListlimit + 1 ).map( j => {
		schema[ `contactlist${ i }${ j }` ] = {
			type: 'string',
		}
		schema[ `contactlink${ i }${ j }` ] = {
			type: 'string',
		}
		return ''
	} ) }
	return ''
} ) }



export const name = 'czgb/contact-details'

export const settings = {
	title: __( 'Contact Details', i18n ),
	description: __( 'Allows you to list various contact information in the Contact Page', i18n ),
	icon: IconListIcon,
	category: 'cartzillagb',
	example: {},
	keywords: [
		__( 'outlet-store', i18n ),
		__( 'CartzillaGb', i18n ),
	],
	attributes: schema,
	supports: {
		inserter: ! disabledBlocks.includes( name ), // Hide if disabled.
	},

	edit,
	save,

	// CartzillaGB modules.
	modules: {
		'advanced-responsive': true,
	},
}