/**
 * External dependencies
 */
import { IconBlock } from '~cartzillagb/icons'
import { range } from '~cartzillagb/util'
import { 
	i18n, disabledBlocks, srcUrl 
} from 'cartzillagb'

/**
 * Internal dependencies
 */
import save from './save'
import edit from './edit'
import DefaultImage  from  './img/img1.png'

/**
 * WordPress dependencies
 */
import { applyFilters } from '@wordpress/hooks'
import { __ } from '@wordpress/i18n'


export const schema = {
	className : {
		type: 'string',
	},
	enableContainer: {
		type: 'boolean',
		default: true,
	},
	enableheader: {
		type: 'boolean',
		default: true,
	},
	enableFooter: {
		type: 'boolean',
		default: true,
	},
	limit: {
		type: 'number',
		default: 12,
	},
	lgColumns : {
		type: 'number',
		default: 4,
	},
	smColumns : {
		type: 'number',
		default: 2,
	},
	mdColumns : {
		type: 'number',
		default: 3,
	},
	heading : {
		type: 'string',
	},
	footertext : {
		type: 'string',
	}
	
}

const maxLimit = applyFilters( 'cartzillagb.features-list-block.edit.inspector.max.limit', 12 )

{ range( 1, maxLimit + 1 ).forEach( i => {

	schema[ `title${ i }` ] = {
		type: 'string',
	}
	schema[ `desc${ i }` ] = {
		type: 'string',
	}
	schema[ `mediaURL${ i }` ] = {
		type: 'string',
		default: `${ srcUrl }/${ DefaultImage }`
	}
} ) }



export const name = 'czgb/features-list-block'

export const settings = {
	title: __( 'Features Block', i18n ),
	icon: IconBlock,
	category: 'cartzillagb',
	example: {},
	keywords: [
		__( 'features-blocks', i18n ),
		__( 'CartzillaGb', i18n ),
	],
	attributes: schema,
	supports: {
		inserter: ! disabledBlocks.includes( name ), // Hide if disabled.
	},

	edit,
	save,

	// CartzillaGB modules.
	modules: {
		'advanced-responsive': true,
	},
}