/**
 * External dependencies
 */
import {
	BlockContainer, ButtonEditHelper, DivBackground,
} from '~cartzillagb/components'
import { withBlockStyles, withUniqueClass } from '~cartzillagb/higher-order'
import classnames from 'classnames'

/**
 * Internal dependencies
 */
import createStyles from './style'

/**
 * WordPress dependencies
 */
import { applyFilters } from '@wordpress/hooks'
import { compose } from '@wordpress/compose'
import { Fragment } from '@wordpress/element'
import { RichText } from '@wordpress/block-editor'

const save = props => {
	const { className } = props
	const {
		design = 'basic',
		shadow = '',

		// Title.
		showTitle = true,
		title = '',
		titleTag = 'h3',

		// Description.
		showDescription = true,
		description = '',

		// Button.
		showButton = true,
		buttonText = '',
		uniqueClass
	} = props.attributes

	const mainClasses = classnames( [
		className,
		'czgb-cta',
		'czgb-cta--v2',
	], applyFilters( 'cartzillagb.cta.mainclasses', {
		[ `czgb-cta--design-${ design }` ]: design !== 'basic',
	}, design, props ) )

	const itemClasses = classnames( [
		'czgb-cta__item',
	], applyFilters( 'cartzillagb.cta.boxclasses', {
		[ `czgb--shadow-${ shadow }` ]: design !== 'plain' && shadow !== '',
	}, design, props ) )

	const titleComp = showTitle && ! RichText.isEmpty( title ) &&
		<RichText.Content
			tagName={ titleTag || 'h3' }
			className="czgb-cta__title"
			value={ title }
		/>
	const descriptionComp = showDescription && ! RichText.isEmpty( description ) &&
		<RichText.Content
			tagName="p"
			className="czgb-cta__description"
			value={ description }
		/>
	const buttonComp = showButton && ! RichText.isEmpty( buttonText ) &&
		<ButtonEditHelper.Content
			attrNameTemplate={ `button%s` }
			blockAttributes={ props.attributes }
		/>

	const comps = {
		title: titleComp,
		description: descriptionComp,
		button: buttonComp,
	}

	return (
		<BlockContainer.Save className={ mainClasses } blockProps={ { ...props, attributes:{ ...props.attributes, anchor: uniqueClass } } } render={ () => (
			<Fragment>
				<DivBackground
					className={ itemClasses }
					backgroundAttrName="column%s"
					blockProps={ props }
				>
					{ applyFilters( 'cartzillagb.cta.save.output', (
						<Fragment>
							{ titleComp }
							{ descriptionComp }
							{ buttonComp }
						</Fragment>
					), comps, props ) }
				</DivBackground>
			</Fragment>
		) } />
	)
}

export default compose(
	withUniqueClass,
	withBlockStyles( createStyles ),
)( save )
