/**
 * External dependencies
 */
import { ImageUploadPlaceholder, IconDisplay } from '~cartzillagb/components'
import {
	withUniqueClass,
} from '~cartzillagb/higher-order'
import classnames from 'classnames'
import { getBootstrapClasses, range } from '~cartzillagb/util'
import { i18n } from 'cartzillagb'

/**
 * WordPress dependencies
 */
import { __ } from '@wordpress/i18n'
import { RichText } from '@wordpress/block-editor'
import { applyFilters } from '@wordpress/hooks'
import { Fragment } from '@wordpress/element'
import { compose } from '@wordpress/compose'

const save = props => {
	const {
		className, attributes,
	} = props

	const {
		enableContainer,
		limit,
		smColumns,
		displaySectionHeader,
        displaySectionTitle,
        displaySectionDesc,
        title,
        subTitle,
        enableIcon
	} = attributes

	const mainClasses = classnames( [
		className,
		'pt-5 pb-3 pb-lg-5',
		'cards-block'
	], applyFilters( 'cartzillagb.cards.main-block.classes', {
		'container': enableContainer,
	}, props ) )


	const content = range( 1, ( limit ) + 1 ).map( i => {

		const mediaURL = attributes[ `mediaURL${ i }` ]
		const name = attributes[ `name${ i }` ]
		const link = attributes[ `link${ i }` ] !== undefined ? attributes[ `link${ i }` ]  : '#'

		let defaultname =''

		if ( (i % 6 ) === 1 ) {
			defaultname=(__("Fashion Store v.1", i18n))            
		}

		if ( (i % 6 ) === 2 ) {
			defaultname=(__("Fashion Store v.2", i18n))
		}

		if ( (i % 6 ) === 3 ) {
			defaultname=(__("Electronics Store", i18n))
		}

		if ( (i % 6 ) === 4 ) {
			defaultname=(__("Multi-vendor Marketplace", i18n))            
		}

		if ( (i % 6 ) === 5 ) {
			defaultname=(__("Single Product / Brand Store", i18n))
		}

		if ( (i % 6 ) === 0 ) {
			defaultname=(__("Grocery Store", i18n))
		}

		const defaulticon ='czi-eye'

		const Icon = attributes[ `icon${ i }` ]

		const imageComp = (
			<ImageUploadPlaceholder
				className={ `czgb-cards__image` }
				imageURL={ mediaURL }
				render={ <img src={ mediaURL } alt={ __( 'Multi-vendor Marketplace' ) } /> }
			/>
		)

		const nameComp = (
			<RichText.Content
				tagName='h3'
				className="product-title font-size-lg pt-2"
				value={ name || defaultname }
				
			/>
		)

		const iconComp = (
			<IconDisplay 
				value={ Icon || defaulticon }
			/>    
		)

		
		const bootstrapClass = getBootstrapClasses( undefined, smColumns, undefined, undefined )

		const output = (
			<div className={ `${ bootstrapClass } mb-4` }  key={ i }>
				<div className="card product-card-alt">
					<div className="product-thumb border">
					{ enableIcon && 
						<div className="product-card-actions">
							<a href={ link } className="btn btn-light btn-icon btn-shadow font-size-base mx-2" target="_blank" rel="noopener noreferrer">
								{ iconComp }
							</a>
						</div>
					}
					<a className="product-thumb-overlay" href={ link } target="_blank" rel="noopener noreferrer"></a>
						{ imageComp }
					</div>
						
					<div className="card-body text-center">
						{ nameComp }                     
					</div>
				</div>
			</div>
		)

		return output
	} )
		

	return (
		<div className={ mainClasses } id="demos">
			<Fragment>
			{ displaySectionHeader &&
				<div className="text-center pt-md-4 pb-2">
					{ displaySectionTitle &&
	                    <RichText.Content
							tagName='h2'
							className="title"
							value={ title || '<span class="font-weight-light">Explore</span>  Cartzilla Demos' }
						/>
	                }

	                { displaySectionDesc &&
	                    <RichText.Content
							tagName='p'
							className="text-muted"
							value={ subTitle || 'Explore the collection of carefully built homepages. More to come soon!' }
						/>
	                }
				</div>
			}
				<div className="row pt-4">
					{ content }
				</div>
			</Fragment>
		</div>
	)  
}


export default compose(
	withUniqueClass,
)( save )